import axios from 'axios';
import * as config from '../../config';

export default {
    name: "login",
    data() {
      return {
          User:{
              email:'',
              password:''
          },
          formCompleted: true //need to setup fields validation, until then leave true
      };
    },
    methods: {
        loginUser() {
            var that = this;
            axios.post(config.default.apiUrl + '/v1/auth/', this.User)
                .then(function (response) {
                    var token = response.data.session.token;
                    that.setCookie("token", token , response.data.session.time)
                    that.config.token = token;
                    that.$router.push({ path: '/' });
                })
                .catch(function (error) {
                    that.console.log(error.message);
                });
        },
        setCookie(cname, cvalue, exdate) {
            var d = new Date(exdate);
            var expires = "expires=" + d.toUTCString();
            document.cookie = cname + "=" + cvalue + ";" + expires + ";path=/";
        }
    },
    mounted() {
      if (document.cookie.indexOf("token") > -1 && this.$route.path == "/login") {
        this.$router.push("/"); // redirect to home, for example
      }
    }
  }