<template>
  <div class="container">
    <section class="section">
      <p class="is-size-1">Logged In!</p>
      <br>
      <div class="has-text-centered">
        <button class="button is-white" @click="logoutUser()">Logout</button>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  name: "home",
  methods: {
      logoutUser(){
        document.cookie = 'token' + '=;expires=Thu, 01 Jan 1970 00:00:01 GMT;';
        this.$router.push("/login");
      }
  },
  mounted() {
    if (document.cookie.indexOf("token") <= -1 && this.$route.path == "/") {
      this.$router.push("/login"); // redirect to home, for example
    }
  }
};
</script>